.hero {
  padding: 20px;
  text-align: center;
  background-color: whitesmoke;
  font-family: 'Times New Roman';
  font-size: 160%;
  font-style: italic;
}
#featured-products {
  padding: 30px;
  background-color: #f5f5f5;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  text-align: center;
}

#featured-products h2 {
  width: 100%;
  text-align: center;
  color: black;
  margin-bottom: 20px;
}

/* Individual product styling */
.product {
  background-color: rgba(255, 255, 255, 0.626);
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  width: 200px;
  padding: 15px;
  box-sizing: border-box;
  transition: transform 0.2s ease;
}

.product:hover {
  transform: scale(1.03);
}

.product img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

.product h3 {
  font-size: 1.1em;
  margin: 5px 0;
}

.product p {
  font-weight: bold;
  margin: 0;
  color: #444;
}
